import * as React from "react";
import * as SliderPrimitive from "@radix-ui/react-slider";
import { cn } from "../../lib/utils";

const Slider = React.forwardRef<
  React.ElementRef<typeof SliderPrimitive.Root>,
  React.ComponentPropsWithoutRef<typeof SliderPrimitive.Root>
>(({ className, ...props }, ref) => (
  <SliderPrimitive.Root
    ref={ref}
    className={cn(
      "relative flex w-full touch-none select-none items-center",
      className
    )}
    {...props}
  >
    <SliderPrimitive.Track className="relative h-1.5 w-full grow overflow-hidden rounded-full bg-neutral-200">
      <SliderPrimitive.Range className="absolute h-full bg-[#FF585D]" />
    </SliderPrimitive.Track>
    <SliderPrimitive.Thumb className="block h-4 w-4 rounded-full border border-white bg-[#FF585D] shadow outline-none focus-visible:ring-2 focus-visible:ring-[#FF585D]/60" />
    {props.max && props.max > 0 && props.defaultValue && props.defaultValue.length === 2 && (
      <SliderPrimitive.Thumb className="block h-4 w-4 rounded-full border border-white bg-[#FF585D] shadow outline-none focus-visible:ring-2 focus-visible:ring-[#FF585D]/60" />
    )}
  </SliderPrimitive.Root>
));
Slider.displayName = SliderPrimitive.Root.displayName;

export { Slider };

